<template>
    <div>
        <!-- 一条文章单元格 -->
        <van-cell>
            <!-- 标题区域的插槽 -->
            <template #title>
                <div class="title-box">
                <!-- 标题 -->
                <span>{{ artObj.title }}</span>
                <!-- 单图 -->
                <img class="thumb" v-if="artObj.cover.type === 1" v-lazy="artObj.cover.images[0]">
                <!-- <van-image class="thumb" 
                v-if="artObj.cover.type === 1" 
                :src="artObj.cover.images[0]" 
                alt="">
                    <template v-slot:error>图片走丢了</template>
                </van-image> -->
                </div>
                <!-- 多图 -->
                <div class="thumb-box" v-if="artObj.cover.type > 1">
                    <img class="thumb" 
                    v-for="(imgUrl,index) in artObj.cover.images" 
                    :key="index" 
                    v-lazy="imgUrl"
                    > 
                    <!-- <van-image class="thumb" 
                        v-for="(imgUrl,index) in artObj.cover.images" 
                        :key="index" 
                        :src="imgUrl"
                        alt="">
                        <template v-slot:error>图片走丢了</template>
                    </van-image> -->
                </div>
            </template>
            <!-- label 区域的插槽 -->
            <template #label>
                <div class="label-box">
                    <div>
                        <span>{{artObj.aut_name}}</span>
                        <span>{{artObj.comm_count}}评论</span>
                        <span>{{formatTime(artObj.pubdate)}}</span>
                    </div>
                    <!-- 反馈按钮 -->
                    <van-icon name="cross" @click.stop="show = true" v-if="isShow"></van-icon>
                </div>
            </template>
        </van-cell>  
        <van-action-sheet 
        v-model="show" 
        :actions="actions" 
        @select="onSelect" 
        @cancel="cancelFn"
        @close="closeFn"
        get-container='body' 
        :cancel-text="bottomText"
        />  
    </div>
</template>

<script>
import { timeAgo } from '@/utils/date.js'
import { firstActions, secondActions } from '@/api/report.js'

export default {
    name: 'ArticleItem',
    props: {
        artObj: Object, // 文章对象
        isShow: {
            type: Boolean,
            default:true // 默认值为true
        }
    },
    data () {
        return {
            show: false, // 反馈面板显示/隐藏
            actions: firstActions, // 反馈面板选项数组
            bottomText:'取消' // 底部按钮的文字
        }
    },
    methods:{
        formatTime: timeAgo, // timeAgo是函数体
        // 反馈面板 - 选项选择事件
        onSelect(action,item) {
            // 默认情况下点击选项时不会自动收起
            // 可以通过 close-on-click-action 属性开启自动收起
            // this.show = false;

            if (action.name === '反馈垃圾内容') {
                this.actions = secondActions
                this.bottomText = '返回'
            } else if (action.name === '不感兴趣') {
                this.$emit('disLikeEV', this.artObj.art_id)
                this.show = false
            } else {
                this.$emit('reportEV', this.artObj.art_id, action.value)
                this.show = false
            }
        },
        // 反馈面板-底部按钮 - 点击事件
        cancelFn() {
            if (this.bottomText === '返回') {
                this.show = true
                this.actions = firstActions
                this.bottomText = '取消'
            }
        },
        // 反馈面板-关闭面板 - 触发事件
        closeFn() {
            this.actions = firstActions
            this.bottomText = '取消'
        },
    }
}
</script>

<style scoped lang="less">
    .title-box{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
    .label-box{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .label-box span{
        margin:  0 3px;
        &:first-child{
            margin-left: 0%;
        }
    }
    /* 图片样式 */
    .thumb{
        width: 113px;
        height: 70px;
        background-color: #f8f8f8;
        object-fit: cover;
    }
    /* 三图 图片容器 */
    .thumb-box{
        display: flex;
        justify-content: space-between;
    }
</style>