<template>
    <div>
        <!-- 头部导航 -->
        <div> 
            <van-nav-bar fixed>
                <template v-slot:left>
                    <img class="logo" :src="imgObj">
                </template>
                <template #right>
                    <van-icon @click="moveSearchPageFn" name="search" size="0.48rem" color="#fff"/>
                </template>
            </van-nav-bar>
        </div>

        <!-- tab导航栏 -->
        <div class="main">
            <van-tabs v-model="channelId" 
            @change="channelChangeFn"
            sticky 
            animated 
            offset-top="1.226667rem"
            >
                <van-tab 
                    :title="obj.name" 
                    v-for="obj in userChannelList" :key="obj.id"
                    :name="obj.id"
                    >
                    <ArticleList :channelId="channelId"></ArticleList>
                </van-tab>
            </van-tabs>
        
            <!-- 编辑频道图标 -->
            <van-icon name="plus" size="0.37333334rem" class="moreChannels" @click="plusClickFn"></van-icon>
            
        </div>

        <!-- 频道管理弹出层 -->
        <van-popup class="channel_popup" v-model="show" get-container="body">
            <ChannelEdit 
            :userList="userChannelList"
            :unCheckList="unCheckChannelList"
            @addChannelEV="addChannelFn"
            @removeChannelEV="removeChannelFn"
            @closeEV="closeFn"
            ref="editRef"
            v-model="channelId"
            ></ChannelEdit>
        </van-popup>
    </div>
</template> 

<script>
import logoPng from '../../assets/tt.png'
import { getUserChannelsAPI,getAllChannelsAPI,updateChannelsAPI,removeTheChannelAPI } from '@/api'
import ArticleList from './components/ArticleList.vue'
import ChannelEdit from './ChannelEdit.vue'
export default {
    name: 'Home',
    components:{ArticleList,ChannelEdit},
    data () {
        return {
            imgObj: logoPng,
            channelId: 0, // tab导航 激活频道的ID(默认为0)
            userChannelList: [], // 用户选择频道列表 
            allChannelList:[], // 所有频道列表
            show: false,  // 频道弹出层的显示/隐藏
            channelScrollTObj:{} // 保存每个频道的滚动位置
        }
    },
    async created() {
        // 频道列表
        const res = await getUserChannelsAPI()
        this.userChannelList = res.data.data.channels

        // 所有频道列表
        const res2 = await getAllChannelsAPI()
        this.allChannelList = res2.data.data.channels
    },
    methods: {
        // tabs切换事件
        channelChangeFn() {
            this.$nextTick(() => {
                document.documentElement.scrollTop = this.channelScrollTObj[this.channelId]
            })
        },
        // +号点击方法
        plusClickFn() {
            this.show = true;
        },

        // 添加频道
        async addChannelFn(channelObj) {
            this.userChannelList.push(channelObj)
            // 去掉推荐频道
            const newArr = this.userChannelList.filter(obj => obj.id !== 0)
            // 数组里对象字段 -> 转换
            let theNewArr = newArr.map((obj, index) => {
                // 浅拷贝
                const newObj = { ...obj }
                delete newObj.name
                newObj.seq = index
                return newObj
            })
            const res = await updateChannelsAPI({
                channels: theNewArr
            })
            console.log(res)
        },
        
        // 删除频道
        async removeChannelFn(channelObj) {
            const index = this.userChannelList.findIndex(obj => obj.id === channelObj.id)
            this.userChannelList.splice(index, 1)

            const res = await removeTheChannelAPI({
                channelId:channelObj.id
            })
            console.log(res)
        },

        // 关闭弹出层
        closeFn() {
            this.show = false
            this.$refs.editRef.isEdit = false
        },

        // 跳转搜索页面
        moveSearchPageFn() {
            this.$router.push('/search')
        },

        // 监听网页滚动事件
        scrollFn() {
            this.$route.meta.scrollT = document.documentElement.scrollTop
            // 同时保存当前频道的滚动距离
            this.channelScrollTObj[this.channelId] = document.documentElement.scrollTop
        }
    },
    computed: {
        //更多频道  (所有频道 - 用户频道) 
        unCheckChannelList() {

            // const newArr = this.allChannelList.filter(bigObj => {
            //     let index = this.userChannelList.findIndex(smallObj => {
            //         return smallObj.id === bigObj.id
            //     })
            //     if (index > -1) {
            //         return false
            //     } else {
            //         return true
            //     }
            // })
            // return newArr

            return this.allChannelList.filter(
                bigObj => this.userChannelList.findIndex(
                    smallObj => smallObj.id === bigObj.id) === -1)
        }
    },
    activated() {
        window.addEventListener('scroll', this.scrollFn)
        document.documentElement.scrollTop = this.$route.meta.scrollT
    },
    deactivated() {
        window.removeEventListener('scroll',this.scrollFn)
    }
   
}
</script>

<style scoped lang="less">
    .logo{
        width: 100px;
        height: 30px;
    }
    .main{
        padding-top: 46px;
    }

    // 设置tabs容器的样式 
    /deep/ .van-tabs__wrap{
        padding-right: 30px;
        background-color: #fff;
    }
    // 设置添加小图标的样式
    .moreChannels{
        position: fixed;
        top: 62px;
        right: 8px;
        z-index: 999;
    }
    .channel_popup{
        width: 100vw;
        height: 100vh;
    }
</style>