<template>
    <div>
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
            <van-list
            v-model="loading"
            :finished="finished"
            :immediate-check="false"
            offset="50"
            finished-text="没有更多了"
            @load="onLoad"
            >
                <ArticleItem 
                v-for="obj in list" 
                :key="obj.art_id"
                :artObj="obj"
                @disLikeEV="disLikeFn"
                @reportEV="reportFn"
                @click.native="itemClickFn(obj.art_id)"
                ></ArticleItem>
            </van-list>
        </van-pull-refresh>
    </div>
</template>

<script>
import ArticleItem from '../../../components/ArticleItem.vue'
import { getAllArticleListAPI,dislikeArticleAPI,reportArticleAPI } from '@/api'
import {Notify} from 'vant'
export default {
    name:'ArticleList',
    components: { ArticleItem },
    props: {
        channelId:Number
    }, 
    data () {
        return {
            list: [],
            loading: false, // 底部加载状态
            finished: false, // 底部完成状态
            theTime: new Date().getTime(),
            isLoading:false // 顶部加载状态
        }
    },
    async created() {
        this.getArticleListFn()
    },
    methods: {
        // 优化代码，专门负责发送请求封装
        async getArticleListFn() {
            const res = await getAllArticleListAPI({
                channel_id: this.channelId,
                timestamp: this.theTime
             })
            this.list = [...this.list,...res.data.data.results]
            this.theTime = res.data.data.pre_timestamp

            // 关闭加载状态
            this.loading = false  
            if (res.data.data.pre_timestamp === null) {
                this.finished = true
                return
            }

            // 关闭顶部加载状态
            this.isLoading = false
        },
        // 底部加载事件方法 (这里会和创建时一起请求，就是请求两次 —— 用到：immediate-check	是否在初始化时立即执行滚动位置检查)
        async onLoad() {
            // 如果页面没有数据，没有高度，让本次onLoad逻辑代码不往下执行了
            if (this.list.length === 0) {
                return
            }
            this.getArticleListFn()
    
        },
        // 顶部刷新数据事件方法
        async onRefresh() {
            this.list = []
            this.theTime = new Date().getTime()
            this.getArticleListFn()
        },
        // 反馈 - 不感兴趣
        async disLikeFn(id) {
            const res = await dislikeArticleAPI({
                artId:id
            })
            console.log(res);
            Notify({type:'success',message:'反馈成功'})
        },
        // 反馈举报
        async reportFn(id,value) {
            const res = reportArticleAPI({
                artId: id,
                type:value
            })
            console.log(res);
            Notify({type:'success',message:'举报成功'})
        },
        // 跳转到详情页
        itemClickFn(id) {
            this.$router.push({
                path:`/detail?art_id=${id}`
            })
        }
    }
}
</script>